@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,700;1,100;1,200;1,300;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sail&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  -webkit-justify-content: space-between !important;
  justify-content: space-between !important;
}

.nav-link {
  background-color: transparent;
  color: #fff !important;
  border: none !important;
  font-family: "Montserrat", sans-serif;
}

.nav-link:hover {
  color: #4e0097 !important;
}

.card {
  margin: 20px;
  border-radius: 10px;
  width: 20%;
  height: 325px;
  transition: all 0.3s ease !important;
  background-color: #111;
  font-family: "Montserrat", sans-serif;
}

p {
  font-size: 1rem;
  color: white;
}

span {
  color: white;
  width: auto;
  font-weight: 900;
  padding: 0 !important;
}

h3 {
  font-size: 20px;
  text-align: start;
  color: #ffffff;
  font-weight: 400;
}

.card:hover {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
  box-shadow: 0 4px 8px 0 rgba(97, 93, 93, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
  cursor: pointer;
  background-color: #111;
}

.d-flex {
  -webkit-justify-content: space-between;
  justify-content: space-between;
}

.link {
  color: #000;
  font-size: 1rem;
  border-radius: 5px;
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
}

.link:hover {
  color: #4e0097;
}

img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.bg {
  width: 100%;
  height: 230px;
  background-color: gray;
  border-radius: 10px;
}

.link {
  border: none;
}

.badge {
  z-index: 5;
}

@media (max-width: 767px) {
  .card {
    margin-bottom: 20px;
    border-radius: 10px;
    width: 50%;
    transition: all 0.3s ease !important;
    background-color: #111;
    margin-top: 10px;
    font-family: "Montserrat", sans-serif;
    padding: 10px;
  }

  .bg {
    width: 100%;
    height: 100px;
    background-color: gray;
  }

  p {
    font-size: 0.8rem;
    color: white;
  }

  .link {
    color: #111111;
    font-size: 0.8rem;
    border-radius: 5px;
  }
}

.contentModal__poster {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.contentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  -webkit-align-self: center;
  align-self: center;
}

.contentModal {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.contentModal::-webkit-scrollbar {
  display: none;
}

.contentModal__about {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  width: 95%;
  height: 90%;
  padding: 10px;
  -webkit-justify-content: space-evenly;
  justify-content: space-evenly;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
}

.contentModal__title {
  height: 12%;
  font-size: 1.2em;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  text-align: center;
}

.contentModal__description {
  display: -webkit-flex;
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 20px;
  border-radius: 20px;
  border: 1px solid#4e0097;
  scrollbar-width: thin;
  text-align: justify;
}

.contentModal__description ::-webkit-scrollbar {
  display: none !important;
}

@media (min-width: 835px) {
  .contentModal__landscape {
    object-fit: contain;
    border-radius: 10px;
    display: none;
  }

  .contentModal__poster {
    display: -webkit-flex;
    display: flex;
    width: 38%;
    border-radius: 20px;
  }

  .contentModal {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
    flex-direction: row;
    -webkit-justify-content: space-around;
    justify-content: space-around;
    padding: 10px;
  }

  .contentModal__about {
    width: 58%;
    height: 100%;
    padding: 0;
  }

  .contentModal__title {
    font-size: 2em;
    color: #4e0097;
    padding-bottom: 10px;
  }

  .contentModal__description {
    font-size: 1em;
    padding-left: 5px !important;
  }

  .contentModal__description ::-webkit-scrollbar {
    display: none !important;
  }
}

